.HistoryCollapseStyle{
    color: #fff;
    width: 900px;
    height: 100%;
    background: rgba(255,255,255,.25);
    backdrop-filter: blur(10px) saturate(1.5);
    padding: 5px;
    border-radius: 10px;
    overflow-y: scroll;
    overflow-x: hidden;
}
.HistoryCollapseStyle::-webkit-scrollbar {
    width: 4px;
}
.HistoryCollapseStyle::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(0,0,0,0.2);
}
.HistoryCollapseStyle::-webkit-scrollbar-track {
    border-radius: 0;
    background: rgba(0,0,0,0.1);
}


.PanelStyle{
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    overflow: hidden;
    color: #fff;
    background: rgba(255,255,255,.25);
    margin-top: 5px;
}
.StatusStyle{
    color:"white";
    background-color: red;
    height:'10px';
